/* 검색 리스트 모듈용 CSS : searching_cat.scss  */
// 전체 공통 CSS 불러오기
@import "../css/common/core";
@import "../css/common/reset";
@import "../css/common/grid12_flex";

/* 리스트 박스 */
.clist {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 1.3%;
}
.clist li {
  width: 24%;
  aspect-ratio: 1/1;
}
/* 상품 이미지 */
.clist li img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}
/* 상품 이름 */
.clist li h3 {
  text-align: center;
  font-size: min(23px, 1.5rem);
  line-height: 1.3;
  padding: 15px;
}
