/* 리셋 CSS - reset.css */
/* 기본 초기화 셋팅 */

/* 글자 크기 Root */
html {
  font-size: 10px;
  /* rem 단위 계산 편의 위해 */
}

/* 기본 초기화 */
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
dl,
dt,
dd,
p,
figure,
figcaption {
  margin: 0;
  padding: 0;
}

/* ul,ol 순번, 블릿 없앰 */
ul,
ol {
  list-style: none;
}

/* 버튼 초기화 */
button {
  cursor: pointer;
  background-color: transparent;
  border: transparent;
}

/* a요소 리셋 */
a {
  text-decoration: none;
  color: #000;
}

/* heading 요소 글자 크기 */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  /* 박스 디자인 시 글자 크기로 인한 영향을 없애기 위한 초기화! */
}

/* 이미지 초기화 */
img {
  /* 이미지 링크 시 서버에서 볼 때 파란 테두리 없애기 */
  border: 0;

  /* 박스에 이미지만 넣을 때 엔터 간극 없애기,
  엔터 간극 4px 하단 공백 없애기 */
  vertical-align: middle;
}
