#search-page{padding:5rem}#search-page .searching{width:250px;height:40px;border-radius:20px;background-color:#efefef;box-sizing:border-box;padding:0 5px}#search-page .searching input{font-size:15px;width:77%;margin-top:9px;margin-left:10px;background-color:rgba(0,0,0,0);border:none}#search-page input:focus{outline:none}#search-page .schbtn{cursor:pointer;margin-top:9px;margin-left:10px}#search-page .big-title{font-size:min(35px,5vw);margin:20px 0}#search-page hr{margin:20px 0;border:0;border-top:1px solid #ddd}#search-page .restit{font-size:min(35px,4vw);margin:10px}#search-page .schbx{display:flex;max-width:1420px;min-height:600px;padding:0;padding-top:20px}#search-page .schopt{width:30%}#search-page .listbx{width:85%}#search-page .sortbx{padding-bottom:15px;overflow:hidden}#search-page .sel{float:right;font-family:"Playfair Display",serif;font-size:20px;font-weight:600;border:none;letter-spacing:4px;background-color:rgba(0,0,0,0)}#search-page .sel:focus{outline:none}#search-page .chkbx h2{margin:15px 0 5px}#search-page .spbtn{display:block;float:right;margin-right:5px;font-size:30px;line-height:25px;cursor:pointer}#search-page .chkbx ol>li{padding:10px;font-size:18px;font-weight:bold}#search-page .chkbx input[type=checkbox]{display:none}#search-page .chklb{float:right;display:block;width:20px;height:20px;background-color:#ccc}#search-page input:checked+.chklb{background-color:#0d6efd;text-align:center;font-weight:bold}#search-page input:checked+.chklb::before{content:"√";color:#fff;line-height:0}